import React, { memo, useEffect, useState } from 'react';
import ConfigProvider from '@/component/base/ConfigProvider';
import Desktop from '@/component/system/Desktop';
import Taskbar from '@/component/system/Taskbar';
import Apps from '@/component/system/Apps';
import useTrackInIt from '@/hooks/useTrack';
import { useSystemStore } from '@/stores';
import * as microAppController from '@/controller/microApp';

const Index = (): React.ReactElement => {
  // 上报
  useTrackInIt();

  const [initLoading, setInitLoading] = useState(true);
  // 初始化主题色
  const theme = useSystemStore((state) => state.theme);
  const toggleTheme = useSystemStore((state) => state.toggleTheme);

  const microAppList = useSystemStore((state) => state.microAppList);
  const setMicroAppList = useSystemStore((state) => state.setMicroAppList);

  // 初始化子应用列表状态
  const initMicroAppList = async () => {
    const result = await microAppController.initMicroAppList(microAppList);
    setMicroAppList(result);
    setInitLoading(false);
  };

  // 初始化
  useEffect(() => {
    // 处理样式
    toggleTheme(theme);
    // 初始化子应用列表状态
    initMicroAppList();
  }, []);

  return (
    <ConfigProvider prefixCls="eleven-os">
      {!initLoading && (
        <Desktop>
          <Taskbar />
          <Apps />
        </Desktop>
      )}
    </ConfigProvider>
  );
};

export default memo(Index);
