import React from 'react';
import App from './app';
import Popup from '@/component/base/Popup';
import MicroPanel from '@/component/base/Panel/Micro';
import { IMicroAppInfo } from '@/microApp';
import { useSystemStore } from '@/stores';

// 博客
const Blog = ({ className }: any) => {
  const getMicroAppInfoByName = useSystemStore((state) => state.getMicroAppInfoByName);
  const microAppInfo: IMicroAppInfo | null = getMicroAppInfoByName('blog') || null;

  return (
    <Popup
      width={1024}
      panel={() => <MicroPanel microAppInfo={microAppInfo} />}
      link={microAppInfo?.entry}
      needTrack
    >
      <div className={className}><App /></div>
    </Popup>
  );
};

export default Blog;
