import { microAppMapByPort } from './const';
import { IS_GITEE, IS_GITHUB, IS_DEV } from '../constants';

export type { IMicroAppInfo } from './type';

export const getMicroAppList = () => {
  return Object.keys(microAppMapByPort).map((port) => {
    const { githubEntry, giteeEntry, ...info } = microAppMapByPort[+port];
    let entry = `http://localhost:${port}`;
    if (IS_GITHUB) {
      entry = githubEntry || entry;
    }
    if (IS_GITEE) {
      entry = giteeEntry || entry;
    }
    return {
      ...info,
      entry,
      port: +port,
    };
  }).filter(item => (IS_DEV || !item.isDev)).sort((a, b) => (+b.port) - (+a.port));
};
