import { IMicroAppInfo } from '@/microApp';

const requestPing = (url: string) => fetch(url)
  .then(response => ({ success: true, data: response.status, message: response.statusText }))
  .catch(error => ({ success: false, data: 0, message: error?.message || 'Failed to fetch' }));

export const initMicroAppList = async (list: IMicroAppInfo[]) => {
  const batchGetMicroAppPing = await Promise.all(list.map(i => requestPing(i.entry || '')));
  return list.map((i, index) => ({
    ...i,
    pingStatus: batchGetMicroAppPing[index].data,
    pingStatusText: batchGetMicroAppPing[index].message,
  }));
};
