import { StateCreator } from 'zustand';
// import setCssVariables from '@/utils/setCssVariables';
import isDarkMode from '@/utils/isDarkMode';
import { IMicroAppInfo, getMicroAppList } from '@/microApp';

export interface SystemSlice {
  theme: 'dark' | 'light';
  fullscreen: boolean;
  lifestyle: 'standard' | 'minimalist';
  toggleTheme: (theme: 'dark' | 'light') => void;
  toggleFullScreen: (v: boolean) => void;
  toggleLifestyle: (lifestyle: 'standard' | 'minimalist') => void;
  microAppList: IMicroAppInfo[];
  setMicroAppList: (microAppList: IMicroAppInfo[]) => void;
  getMicroAppInfoByName: (name: string) => IMicroAppInfo | null;
  getMicroAppListByTypeIsApp: () => IMicroAppInfo[];
  getMicroAppListByTypeIsGame: () => IMicroAppInfo[];
  getMicroAppListByTypeIsOther: () => IMicroAppInfo[];
  getMicroAppListByShowroom: () => IMicroAppInfo[];
};

export const createSystemSlice: StateCreator<SystemSlice> = (set, get) => ({
  theme: isDarkMode() ? 'dark' : 'light',
  fullscreen: false,
  lifestyle: 'standard',
  toggleTheme: (theme) => {
    // setCssVariables(theme === 'dark' ? themeDark : themeLight);
    document.documentElement.setAttribute('data-theme', theme);
    set(() => ({ theme }));
  },
  toggleFullScreen: (v) => {
    set(() => {
      return { fullscreen: v };
    });
  },
  toggleLifestyle: (lifestyle) => {
    set(() => ({ lifestyle }));
  },
  microAppList: getMicroAppList(),
  setMicroAppList: (microAppList) => (set({ microAppList })),
  // 根据 name 获取详情
  getMicroAppInfoByName: (name) => {
    const result = get().microAppList.find(i => i.name === name) || null;
    return (result?.pingStatus && result.pingStatus >= 200 && result.pingStatus < 300) ? result : null;
  },
  // 子应用列表 - type === 'app'
  getMicroAppListByTypeIsApp: () => get().microAppList.filter(i => i.type === 'app'),
  // 子应用列表 - type === 'game'
  getMicroAppListByTypeIsGame: () => get().microAppList.filter(i => i.type === 'game'),
  // 子应用列表 - type === 'other'
  getMicroAppListByTypeIsOther: () => get().microAppList.filter(i => i.type === 'other'),
  // 子应用列表 - showroom === true
  getMicroAppListByShowroom: () => get().microAppList.filter(i => (i?.pingStatus && i.pingStatus >= 200 && i.pingStatus < 300) && i.showroom),
});
