import React from 'react';
import App from './app';
import { useSystemStore } from '@/stores';

// 应用中心
const Applications = ({ className }: any) => {
  const getMicroAppListByTypeIsApp = useSystemStore((state) => state.getMicroAppListByTypeIsApp);
  const microAppListByTypeIsApp = getMicroAppListByTypeIsApp();
  return (
    <div className={className}><App data={microAppListByTypeIsApp} /></div>
  );
};

export default Applications;
