import React from 'react';
import App from './app';
import { useSystemStore } from '@/stores';

// 游戏中心
const Games = ({ className }: any) => {
  const getMicroAppListByTypeIsGame = useSystemStore((state) => state.getMicroAppListByTypeIsGame);
  const microAppListByTypeIsGame = getMicroAppListByTypeIsGame();
  return (
    <div className={className}><App data={microAppListByTypeIsGame} /></div>
  );
};

export default Games;
