import React from 'react';
import { Menu } from 'antd';
import type { MenuProps } from 'antd';
import Icon from '@/component/base/Icon';
import { guid } from '@/utils/tool';
import { useSystemStore } from '@/stores';
import styles from './index.less';
import AboutMenu from '../AboutMenu';

const getMenuItems = (items: any[]) => {
  return items.map(item => {
    if (item?.type === 'divider') {
      return item;
    }
    const _items: any = { key: item.key || guid() };
    if (item?.type === 'group') {
      _items.label = <div style={{ fontSize: 11, minWidth: 148 }}>{item.title}</div>;
      _items.type = item.type;
    } else {
      _items.label = <div style={{ fontSize: 13, minWidth: 72 }}>{item.title}</div>;
    }
    let icon = item.src || item.icon;
    // let iconColor = item?.backgroundColor;
    if (item?.type === 'url') {
      icon = 'LinkOutlined';
    }
    if (item.icon !== null) {
      _items.icon = (
        <Icon
          name={icon || 'PaperClipOutlined'}
          size={14}
          style={{
            // background: item?.backgroundColor,
            // color: iconColor,
            color: 'currentColor',
            borderRadius: '2px',
            objectFit: 'cover',
            marginRight: '8px',
          }}
        />
      );
    }
    if (item.items) {
      _items.children = getMenuItems(item.items);
    }
    return _items;
  });
};

const DropdownContent = () => {
  const getMicroAppListByTypeIsApp = useSystemStore((state) => state.getMicroAppListByTypeIsApp);
  const getMicroAppListByTypeIsGame = useSystemStore((state) => state.getMicroAppListByTypeIsGame);
  const getMicroAppListByTypeIsOther = useSystemStore((state) => state.getMicroAppListByTypeIsOther);
  const items: MenuProps['items'] = getMenuItems([
    {
      title: <AboutMenu />,
      icon: null,
    },
    {
      title: '系统偏好设置',
      icon: null,
    },
    {
      type: 'divider',
    },
    {
      title: '快速入口',
      type: 'group',
      items: [
        {
          title: '应用',
          icon: 'RobotOutlined',
          items: getMicroAppListByTypeIsApp(),
        },
        {
          title: '游戏',
          icon: 'CrownOutlined',
          items: getMicroAppListByTypeIsGame(),
        },
        {
          title: '其他',
          icon: 'MacCommandOutlined',
          items: getMicroAppListByTypeIsOther(),
        },
      ],
    },
  ]);
  const onClick: MenuProps['onClick'] = e => {
    console.log('click', e);
  };

  return (
    <div className={styles.dropdownContent}>
      <Menu onClick={onClick} mode="vertical" items={items} theme={'dropdownMenuPopup' as any} />
    </div>
  );
};

export default DropdownContent;
