import React, { useState } from 'react';
import App from './app';
import Popup from '@/component/base/Popup';
import MicroPanel from '@/component/base/Panel/Micro';
import IframePanel from '@/component/base/Panel/Iframe';
import { IMicroAppInfo } from '@/microApp';
import { useSystemStore } from '@/stores';

export const data = {
  main: { type: 'micro', name: '文件上传', key: 'micro-app-files' },
  sub: [
    { type: 'iframe', name: '图片压缩', url: 'https://tinypng.com/' },
    { type: 'iframe', name: '智能配音', url: 'https://zenvideo.qq.com/smart/tts' },
    { type: 'iframe', name: '图片编辑', url: 'https://tool.browser.qq.com/img_edit_canvas.html' },
    { type: 'iframe', name: '视频编辑', url: 'https://www.capcut.cn/web' },
  ]
};

interface TPopupInfo {
  type: 'iframe' | 'micro';
  iframeInfo?: { name: string; url: string; },
  microInfo?: IMicroAppInfo | null;
};

// 图床
const Files = ({ className }: any) => {
  const getMicroAppInfoByName = useSystemStore((state) => state.getMicroAppInfoByName);
  const [popupInfo, setPopupInfo] = useState<TPopupInfo | null>(null);
  const onClickItem = (item: any) => {
    const options: TPopupInfo = { type: item.type };
    if (item.type === 'iframe') { options.iframeInfo = item; }
    if (item.type === 'micro') { options.microInfo = getMicroAppInfoByName(item.key); }
    setPopupInfo(options);
  };
  return (
    <Popup
      width={1024}
      panel={() => popupInfo && (
        popupInfo.type === 'iframe' ? <IframePanel item={popupInfo.iframeInfo as { name: string; url: string; }} /> : <MicroPanel microAppInfo={popupInfo.microInfo as IMicroAppInfo | null} />
      )}
      link={(popupInfo as any)?.iframeInfo?.url || (popupInfo as any)?.microInfo?.entry}
      needTrack
    >
      <div className={className}><App data={data} onClickItem={(item: any) => onClickItem(item)} /></div>
    </Popup>
  );
};

export default Files;
