import React from 'react';
import DefaultCard from '@/component/base/Card/DefaultCard';
import styles from './app.less';
import MicroPanel from '@/component/base/Panel/Micro';
import { IMicroAppInfo } from '@/microApp';
import { useSystemStore } from '@/stores';

// 番茄
const App: React.FC = () => {
  const getMicroAppInfoByName = useSystemStore((state) => state.getMicroAppInfoByName);
  const microAppKey = 'micro-app-tomato';
  const microAppInfo: IMicroAppInfo | null = getMicroAppInfoByName(microAppKey) || null;
  if (microAppInfo) {
    microAppInfo.props = { view: 'showroom', entryUrl: microAppInfo.entry };
  }
  return (
    <DefaultCard className={styles.warp}>
      <MicroPanel
        id={`${microAppKey}-showroom`}
        microAppInfo={microAppInfo}
        loadingText={null}
        errorType={'mini'}
      />
    </DefaultCard>
  );
};

export default App;
