import React from 'react';
import DefaultCard from '@/component/base/Card/DefaultCard';
import styles from './app.less';
import MicroPanel from '@/component/base/Panel/Micro';
import { IMicroAppInfo } from '@/microApp';
import { useSystemStore } from '@/stores';

const App: React.FC = () => {
  const getMicroAppListByShowroom = useSystemStore((state) => state.getMicroAppListByShowroom);
  const showroomList = getMicroAppListByShowroom();
  const randomIndex = Math.floor(Math.random() * showroomList.length) || 0;
  const microAppInfo: IMicroAppInfo | null = showroomList[randomIndex] || null;
  if (microAppInfo) {
    microAppInfo.props = { view: 'showroom' };
  }
  return (
    <DefaultCard className={styles.warp}>
      <MicroPanel id={`${microAppInfo?.name}-showroom`} microAppInfo={microAppInfo} />
    </DefaultCard>
  );
};

export default App;
