import React, { useState } from 'react';
import classnames from 'classnames';
import DefaultCard from '@/component/base/Card/DefaultCard';
import styles from './app.less';
import {
  // myList,
  defaultList,
} from './data';
// import { getQuery } from '@/utils/tool';
// import { IS_DEV } from '@/constants';

// 热点新闻
const App: React.FC = () => {
  // const { type } = getQuery();
  const data = [
    // ...(((type === 'xhzy' || IS_DEV) && myList) || []),
    ...defaultList
  ];
  const [curDataName, setCurDataName] = useState(data[0].name);
  const [curList, setCurList] = useState(data[0].children);

  const handleClickTag = (name: string, children: any[]) => {
    setCurDataName(name);
    setCurList(children);
  };

  return (
    <DefaultCard className={styles.nav}>
      <div className={styles.header}>
        {data.map(item => (
          <div
            key={item.name}
            className={classnames(styles.tag, curDataName === item.name && styles.active)}
            onClick={() => handleClickTag(item.name, item.children)}
          >
            {item.name}
          </div>
        ))}
      </div>
      <div className={styles.content} key={`${curDataName}-list`}>
        {curList.map((item, index) => (
          <a className={styles.item} key={`${item.name}-${index}`} href={item.url} target="_blank">
            <div className={styles.num}>{index + 1}</div>
            <div className={styles.title}>{`${item.name} | ${item.desc || item.url}`}</div>
          </a>
        ))}
      </div>
    </DefaultCard>
  );
};

export default App;
