import React, { useEffect, useState } from 'react';
import { loadMicroApp } from 'qiankun';
import { Spin, Result, Typography } from 'antd';
import { IMicroAppInfo } from '@/microApp';
import Orbit from '@/component/base/Orbit';
import styles from './index.less';

type IProps = {
  id?: string;
  microAppInfo: IMicroAppInfo | null;
  loadingText?: string | null;
  errorType?: 'default' | 'mini';
}

const Panel = ({
  microAppInfo,
  id = 'micro-app-container',
  loadingText = '加载中',
  errorType,
}: IProps) => {
  let microApp: any = null;
  const [initLoading, setInitLoading] = useState(microAppInfo !== null);
  const [isError, setIsError] = useState(false);

  // 处理参数透传问题 props 字段会被合并掉
  let _microAppInfo: any = {};
  const _props = { ...microAppInfo?.props };
  delete microAppInfo?.props;
  _microAppInfo = {
    ...microAppInfo,
    props: { props: _props },
    container: `#${id}`,
  };
  console.log('_microAppInfo', _microAppInfo);

  useEffect(() => {
    if (microAppInfo) {
      microApp = loadMicroApp(_microAppInfo, {
        // 部分第三方动态加载的资源（css/js) 不被劫持处理
        excludeAssetFilter: (assetUrl: string) => {
          const url = assetUrl.replace(/^http(s)?:\/\//, '');
          if (!(
            url.startsWith(window.location.hostname) ||
            url.startsWith('localhost')
          )) {
            console.log('assetUrl', assetUrl);
            return true;
          }
          return false;
        }
      });
    }
    if (microApp) {
      microApp.mountPromise.then(() => {
        setInitLoading(false);
      }).catch((error: any) => {
        setInitLoading(false);
        setIsError(true);
      });
    }
    return () => {
      if (microApp) {
        microApp.mountPromise.then(() => {
          microApp.unmount();
        }).catch(() => { });
      }
    };
  }, []);

  return (
    <div className={styles.microPanel}>
      <Spin spinning={initLoading} indicator={<Orbit text={loadingText} />}>
        <div id={id} className={"microAppContainer"} style={{ display: !(initLoading || isError || !microAppInfo) ? 'block' : 'none' }}></div>
        {!initLoading && isError && (
          <div
            style={{
              display: 'flex',
              height: '100%',
              justifyContent: 'center',
              alignItems: 'center',
              flexWrap: 'nowrap',
            }}
          >
            {errorType !== 'mini' ? (
              <Result
                status="error"
                title="抱歉，应用加载失败"
                subTitle={(
                  <span>请检查应用是否可运行 <Typography.Link href={microAppInfo?.entry} target="_blank">{microAppInfo?.entry}</Typography.Link></span>
                )}
              >
              </Result>
            ) : (
              <span>应用未启动</span>
            )}
          </div>
        )}
      </Spin>
    </div>
  );
};

export default Panel;
